import React, {Component} from 'react';
import Navbar from 'react-bootstrap/Navbar';
import logo from '../../images/48x48w.png';


class NavBar extends Component {
    render(){
        return(
            <Navbar sticky="top" bg="dark" variant='dark' expand="lg" className="w-100" style={{height: '58px'}}>
                <Navbar.Brand href="/home" className="">
                    <img
                        src={logo}
                        width="40"
                        height="40"
                        className="d-inline-block"
                        alt="logo"
                    />
                </Navbar.Brand>
                <Navbar.Brand href="/home" style={{fontSize: '18px'}}>Order Now</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                </Navbar.Collapse>
            </Navbar>
        );
    }
}
export default NavBar;
