import React, {Component} from 'react';
import NavBar  from './layout/navBar';
import BottomBar  from './layout/bottomBar';

class noUrlFound extends Component {
  render() {
    return (
      <div className="noUrlFound">
        <NavBar/>
          <div style={{display: 'flex', justifyContent: 'center', marginTop: '50px'}}>This page is not available</div>
        <BottomBar/>
      </div>
    );
  }
}

export default noUrlFound;